import React from 'react'
import { useState } from 'react'
import Main from './Main'
export default function Increment() {
    let [inc,setInc] = useState(0)
  return (
    <div>

  

        <p>$ {inc}</p>
          
       <Main coin = {inc}/>
       <button onClick={(()=>{
          setInc(inc-1)
       })}>add-1</button>
       <button onClick={(()=>{
           setInc(inc+1)
       })}>sub+1</button>
     


    </div>
  )
}
