import react,{useState} from 'react'
import './App.css'
import './Appm.css'
export default function Nav(props) {
let [dis,fn] = useState(false)
 let trans=()=>{
 fn(!dis)
//  console.log(dis)
 }

 let click1=()=>{
  fn(!dis)
  props.one()
 }

 let click2=()=>{
  fn(!dis)
  props.two()
 }

 let click3=()=>{
  fn(!dis)
  props.three()
 }

 let click4=()=>{
  fn(!dis)
  props.four()

 }
 let click5=()=>{
  fn(!dis)
  props.five()

 }

  return (
    <section>
      <nav className='nav'>
        <div className='lnav'><span>Logo</span> <ul><li> <i onClick={trans} className="fa-solid fa-bars " ></i></li></ul></div>
        <div className={dis?'rnav':'dnone rnav' }>
          <ul >
            <a href="#" onClick={click1} ><li>To do list</li></a>
            <a href="#" onClick={click2}><li>Car-details </li></a>
            <a href="#" onClick={click3}><li>Fan</li></a>
            <a href="#" onClick={click4}><li>Calculator</li></a>
            <a href="#" onClick={click5}><li>Increment</li></a>
          </ul>
        </div>
      </nav>
    </section>
  )
}