import { useState } from 'react'
import './App.css'
export default function Calculate() {
    let [state, setstate] = useState('')
    let [store, setStore] = useState('')
    let [previous, setPrevious] = useState('')
    let [current, setCurrent] = useState('')

    let fn = (e) => {
        setstate(state + e.target.innerText)
             
     

    }

    let delet = () => {
        setstate(state.slice(0, -1))
    }
    let clear = () => {
        setstate('')
    }


 let operation =()=>{
   setstate(eval(state))
 }













    return (
        <>
            <table  >
                <tr>
                    <th colspan='2'><input type="text" placeholder='Please Calculate ' value={state} /></th>
                    <th ><button onClick={delet} >del</button></th>
                    <th ><button onClick={clear} >clear</button></th>
                </tr>
                <tr>
                    <td><button onClick={fn}>1</button></td>
                    <td><button onClick={fn}>2</button></td>
                    <td><button onClick={fn}>3</button></td>
                    <td><button onClick={fn}>/</button></td>
                </tr>

                <tr>
                    <td><button onClick={fn}>4</button></td>
                    <td><button onClick={fn}>5</button></td>
                    <td><button onClick={fn}>6</button></td>
                    <td><button onClick={fn}>-</button></td>
                </tr>
                <tr>
                    <td><button onClick={fn}>7</button></td>
                    <td><button onClick={fn}>8</button></td>
                    <td><button onClick={fn}>9</button></td>
                    <td><button onClick={fn}>+</button></td>
                </tr>

                <tr>
                    <td><button onClick={fn}>.</button></td>
                    <td><button onClick={fn}>0</button></td>
                    <td><button onClick={operation}>=</button></td>
                    <td><button onClick={fn}>*</button></td>
                </tr>


            </table>
        </>
    )

}