// import logo from './logo.svg';
import react, { useState } from 'react';
import './App.css';
import Nav from './Nav';
import One from './projectFile/todo/One';
import Two from './projectFile/car data srore/Two'
import Three from './projectFile/Three';
import Calculate from './projectFile/calculator/Calculate';
import Increment from './projectFile/increment/Increment';
function App() {


  let [state, setState] = useState(1)
  let ones = () => {
    setState(1)
  }

  let twos = () => {
    setState(2)
  }


  let threes = () => {
    setState(3)
  }
  let four = ()=>{
setState(4)
  }

  let five = ()=>{
    setState(5)
      }
  return (
    <div>
      <Nav one={ones} two={twos} three={threes} four = {four} five={five}/>


      {
        ((() => {
          if (state == 1) {
            // To-do List
            return <One />

          } else if (state == 2) {
            return <Two />
          } else if (state == 3) {
            return <Three />
          } else if (state == 4){
            return <Calculate />  
          }
          else if (state == 5){
            return <Increment/>  
          }
        })())
      }




    </div>



  );
}

export default App;
