import img from './fan.png'
import './app.css'
import { useState } from 'react'
export default function Three(){
    let [state,setstate]=useState(false)
    let fn=()=>{
        setstate(!state)
    }
    return(
        <div className={'mdiv'}>
         <img src={img} onClick={fn} className={state?'img':'imgRotate'} alt=""  />
        </div>
    )
}