import React, { useState } from 'react'
import List from './List'


export default function () {

    let [stat1, setstate1] = useState('')
    let [list, push] = useState([])
    let [inpColor,updateColor]=useState({color:'black'})

    let fn = (e) => {
        setstate1(e.target.value)
        updateColor({color:'black'})
    }


    let submit = (e) => {
        e.preventDefault();
        push((olditem) => {

            for (let i in olditem){
                if (olditem[i]===stat1) {
                    updateColor({color:'red'})
                    setstate1("already existing *")
                    return [...olditem]
                   }
            }
             
           setstate1('')
            return [...olditem, stat1]
        })

       

    }


    let delate = (e) => {
        // let x = prompt('Input it')
        let val = e.target.value
        push((olditem) => {
            for (let j = 0; j < olditem.length; j++) {
                if (olditem[j] == val) {
                    olditem.splice(j, 1)
                }

            }
            return [...olditem]
        })




    }
    let edit = (e) => {
        // console.log(e.target.value)
        // console.log(e.target.value)
        let x = prompt('Input it')
        push((olditem) => {
            for (let i = 0; i < olditem.length; i++) {
                if (olditem[i] == e.target.value) {
                    olditem[i] = x
                    console.log(olditem[i])
                }
            }
            return [...olditem]
        })
    }


    return (
        <div>
            <form className='mt-4' onSubmit={submit}>


                <div className="input-group mb-3">
                    <input type="text" className='form-control' placeholder='ada task' onChange={fn} value={stat1} style={inpColor}/>
                    <button type='submit' className="input-group-text bg-primary text-white" id="basic-addon2">add it</button>
                </div>
            </form>


            {
                list.map((taskAdd, i) => {
                    return <List key={i} i={taskAdd} task={taskAdd} delate={delate} j={taskAdd} edit={edit} />
                })
            }

        </div>
    )
}




