import React from 'react'

export default function List(props) {
    return (
        <div>
            <div className="row border mt-1 p-2">
                <div className="col-md-8 my-auto ">{props.task}</div>
                <div className="col-md-4 ">
                    <div className="row mt-3 mt-md-0">
                        <div className="col-4 "><button className='btn btn-danger' onClick={props.delate} value={props.j} >Delate</button></div>
                        <div className="col-4 "><button className='btn btn-success' value={props.i} onClick={props.edit}>Edit it</button></div>
                    </div>
                </div>
            </div>
        </div>
    )
}
